	methods: {
		getAnnouncement: function() {
			uni.request({
				header: {
					'sessionId': 'fa2e5c5698b0480eb68ec05fa9a35a3d'
				},
				url: 'https://test-yi013.weilian.cn/bulletin/queryBulletinTitlesByPage?pageNum=1&length=10',
				success: function(res) {
					console.log(res);
					const data = res.data.data[0].results;
					const data_list = data.map((news) => {
						return {
							id: news.id,
							title: news.title,
							starttime: friendlyDateAndMonth(new Date(news.starttime).getTime())
						};
					});
					_self.announcementList = data_list;
				}
			})
		},
